<template>
<div class="content">
    <el-breadcrumb separator="/">
        <el-breadcrumb-item>URL</el-breadcrumb-item>
        <el-breadcrumb-item>
            <a href="/">URL</a>
        </el-breadcrumb-item>
    </el-breadcrumb>
    <div>
        <!--  -->
        <div class="seach">
            <div class="seach_top">
                <el-input placeholder="URLID" v-model="input" class="input-with-select" @keyup.enter.native="onSubmitInput" maxlength="70">
                    <i slot="prefix" class="el-input__icon el-icon-search"></i>
                </el-input>
                <div class="seach_top_right" @click="option_display()">
                    
                    <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
                </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
                <span></span>
                <el-select v-model="value" placeholder="" @change="onchangeTab">
                    <el-option v-for="(item, index) in options" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>

                <span></span>
                <el-date-picker v-model="value1" type="datetimerange" :picker-options="pickerOptions" range-separator="" start-placeholder="" end-placeholder=""></el-date-picker>
                <div class="seach_bottom_btn">
                    <el-button type="primary" plain size="mini" @click="seachuser()"></el-button>
                    <el-button plain size="mini" @click="reset()"></el-button>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="con_lable">
            <div style="padding:10px;display: flex;justify-content: space-between;">
                <div>
                    <el-button type="primary" @click="addUrl">
                        URL
                        <span class="el-icon-circle-plus-outline"></span>
                    </el-button>
                    <el-button type="primary" plain @click="onImport">URL</el-button>
                    <el-button type="primary" plain @click="setdomainlist"></el-button>
                </div>
                <div>
                    <el-button type="primary"  @click="toexportExcel"></el-button>
                </div>
            </div>

            <!--  -->
            <el-table stripe ref="multipleTable" @selection-change="handleSelectionChange" @sort-change='tableSortChange' :data="tableData" tooltip-effect="dark" style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass">
                <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column  label="URL">
                    <template slot-scope="scope">
                        <span style="width: 190px; float:left;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{scope.row.url}}</span>

                    </template>
                </el-table-column>
                <el-table-column prop="url_name" label=""></el-table-column>
                <el-table-column prop="buser_id" label="ID"></el-table-column>
                <el-table-column prop="state" label="" :formatter="formatState"></el-table-column>
                <el-table-column prop="create_time" label="" sortable="custom"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <span @click="goLink" style="cursor: pointer;"><img width="20" height="20" src='../../assets/img/urlIcon.png' /></span>
                    </template>
                </el-table-column>

                <el-table-column label="" width="350">
                    <template slot-scope="scope">
                        <el-button @click="Configuration(scope.row)" type="text" size="small"></el-button>
                        <el-button type="text" size="small" @click="updatauser(scope.row)"></el-button>
                        <el-button type="text" size="small" @click="monitor(scope.row)"></el-button>
                        <el-button type="text" size="small" @click="onDisable(scope.row)">{{scope.row.state==1?"":""}}</el-button>
                        <el-button type="text" size="small" @click="handleClick(scope.row)"></el-button>
                        <el-button type="text" size="small" @click="deleateuser1(scope.row)" style="color:red;"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <div style="margin-top: 20px;display: flex;justify-content: space-between;align-items: center;">
                <div>
                    <el-button @click="enableuser()" type="text"></el-button>
                    <el-button @click="disableuser()" type="text" style="margin-left:30px;color:red;"></el-button>
                    <el-button @click="deleateuser()" type="text" style="margin-left:30px;"></el-button>
                </div>
                <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
            </div>
        </div>

    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" custom-class="customWidth" class="domain_dialog">
        <el-form :model="dynamicValidateForm" ref="dynamicValidateForm">

            <el-form-item label="ID:" :label-width="formLabelWidth" prop="account" :rules="{
      required: true, message: 'ID', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.buser_id" placeholder="ID" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="URL:" :label-width="formLabelWidth" prop="pwd" placeholder="URL" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.url" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth" prop="conpwd" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.url_name" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="radio" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-select v-model="dynamicValidateForm.url_type" placeholder="" style="width:100%;">
                    <el-option v-for="(item, index) in videoArr" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=":" :label-width="formLabelWidth" prop="conpwd" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-input v-model="dynamicValidateForm.label" placeholder="" autocomplete="off"></el-input>
            </el-form-item>

            <el-form-item label="" :label-width="formLabelWidth" prop="radio" :rules="{
      required: true, message: '', trigger: 'blur'
    }">
                <el-select v-model="dynamicValidateForm.label2" placeholder="" style="width:100%;">
                    <el-option v-for="(item, index) in yewu" :key="index" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>

        </el-form>

        <div slot="footer" class="dialog-footer" style="text-align:center;">
            <el-button @click="dialogFormVisibles"> </el-button>
            <el-button type="primary" @click="onSubmitAdd"> </el-button>
        </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialog" custom-class="customWidth" width="50%" style="width:100%:background:red;">
        <el-table ref="multipleTable" :data="tableData">
            <el-table-column prop="dominds" label=""></el-table-column>
            <el-table-column prop="camesd" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button @click="handleClicks(scope.row)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialog=false"> </el-button>
        </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialupdata" custom-class="customWidth" width="900px">
        <p style="text-align: left;margin: 10px 0;"></p>
        <el-steps :active="actives" finish-status="success" align-center>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
            <el-step title=""></el-step>
        </el-steps>

        <div v-if="actives===1">
            <el-table ref="multipleTable" :data="futableData" tooltip-effect="dark" border style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="configuration" label=""></el-table-column>
                <el-table-column prop="nowconfiguration" label="" show-overflow-tooltip></el-table-column>
            </el-table>
            <el-button style="margin-top: 12px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 12px;" @click="fureset"></el-button>
        </div>
        <div v-else-if="actives===2">
            <div style="display: flex;justify-content: space-between;align-items: center;margin: 10px 0">
                <span style="font-weight: 600;"></span>
                <div>
                    
                    <span>{{nums}}</span>50
                </div>
                <el-input placeholder="" v-model="fuinput" style=" width:300px" class="input-with-select" @keyup.enter.native="onSubmit"></el-input>
            </div>
            <div>
                <el-table ref="multipleTable" :data="urllist" tooltip-effect="dark" border style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column prop="url" label="URL"></el-table-column>
                </el-table>
            </div>
            <el-button style="margin-top: 12px;" type="primary" @click="last"></el-button>
            <el-button style="margin-top: 12px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 12px;" @click="fureset"></el-button>
        </div>
        <!-- <div v-else>
            <el-button style="margin-top: 12px;" type="primary" @click="last"></el-button>
            <el-button style="margin-top: 12px;"  @click="fureset"></el-button>
          </div>-->
    </el-dialog>
    <!---->
    <el-dialog :visible.sync="dialogVisible4" width="25%">
        <div class="addaccout">
            <el-form :model="ruleForm4" ref="ruleForm3" label-position="left" class="demo-ruleForm">
                <h3 class="title"></h3>
                <el-form-item>
                    <el-form-item label="URL:">
                        <el-input v-model="ruleForm4.url" :disabled="true"></el-input>
                    </el-form-item>
                </el-form-item>
                <el-form-item>
                    <el-form-item label=":">
                        <el-input v-model="ruleForm4.url_name" :disabled="true" placeholder="4-20"></el-input>
                    </el-form-item>
                </el-form-item>
                <el-form-item prop="phone">
                    <el-form-item label="ID:">
                        <el-input v-model="ruleForm4.buser_id" :disabled="true" placeholder="11"></el-input>
                    </el-form-item>
                </el-form-item>
                <el-form-item prop="phone">
                    <el-form-item label=":">
                        <el-input v-model="ruleForm4.status" :disabled="true" placeholder="11"></el-input>
                    </el-form-item>
                </el-form-item>
                <el-form-item prop="phone">
                    <el-form-item label=":">
                        <el-input v-model="ruleForm4.create_time" :disabled="true" placeholder="11"></el-input>
                    </el-form-item>
                </el-form-item>
                <el-form-item style="width:100%;display: flex;justify-content:center;">
                    <el-button type="primary" @click="dialogVisible4 = false"></el-button>
                    <el-button @click="dialogVisible4 = false"></el-button>
                </el-form-item>
            </el-form>
        </div>
    </el-dialog>

</div>
</template>

<script>
import fenye from "@/components/fenye";
import common from '../../comm/js/util'
import {
    query_url,
    add_url,
    delete_url,
    change_state
} from "../../servers/api";
export default {
    data() {
        //

        //
        var validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else {
                if (this.ruleForm.checkPass !== "") {
                    this.$refs.ruleForm.validateField("checkPass");
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === "") {
                callback(new Error(""));
            } else if (value !== this.ruleForm.pass) {
                callback(new Error("!"));
            } else {
                callback();
            }
        };
        return {
            actives: 1,
            nums: 12,
            fuinput: "",
            ruleForm: {
                pass: "",
                checkPass: "",
                account: ""
            },

            futableData: [{
                    configuration: "HOST",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "",
                    nowconfiguration: ""
                },
                {
                    configuration: "Refer",
                    nowconfiguration: ""
                },
                {
                    configuration: "URL",
                    nowconfiguration: ""
                },
                {
                    configuration: "IP/",
                    nowconfiguration: ""
                }
            ],
            //
            rules: {
                pass: [{
                    validator: validatePass,
                    trigger: "blur"
                }],
                checkPass: [{
                    validator: validatePass2,
                    trigger: "blur"
                }]
            },
            input: "", //
            value1: "",
            dialogFormVisible: false,
            dialogVisible4: false,
            dialog: false,
            dialupdata: false,
            dialpwdset: false,
            radio: "1",
            radioes: "1",
            isIndeterminate: "",
            sadioes: 1,
            currentPage: 1,
            pagesize: 10,
            total_cnt: 0,
            dynamicValidateForm: {
                buser_id: "",
                url: "",
                url_name: "",
                url_type: "",
                label: "",
                label2: "",

            },
            details: {},
            uetails: {},
            formLabelWidth: "60px",
            optiondisplay: false,
            rotate: false,
            gridData: [],
            tolpage: 1,
            options: [{
                    value: -1,
                    label: ""
                }, {
                    value: 1,
                    label: ""
                },
                {
                    value: 0,
                    label: ""
                },
                // {
                //     value: 2,
                //     label: ""
                // },
                // {
                //     value: 3,
                //     label: ""
                // },
                // {
                //     value: 4,
                //     label: ""
                // },
                // {
                //     value: 5,
                //     label: ""
                // }
            ],
            yewu: [{
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 2,
                    label: ""
                }
            ],
            videoArr: [{
                    value: 0,
                    label: "MP4"
                },
                {
                    value: 1,
                    label: "hls"
                },
                {
                    value: 2,
                    label: "flv"
                }
            ],
            value: "",

            tableData: [],
            urllist: [{
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                },
                {
                    url: "http://www.xxx.cn.abc"
                }
            ],
            multipleSelection: [],
            buser_id: "",
            nowurl: "",
            ruleForm4: {},
            order: 0,
            pageActive: 0,
            tableData: [],
            tableData2: [],
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now() - 8.64e6; //-8.64e6
                },
            }
        };
    },
    components: {
        fenye
    },
    created() {},
    mounted() {
        this.queryUrlList();
    },
    methods: {
        //
        monitor(){
            this.$router.push({
                path:"/nodeMap1"
            })
        },
        //
        onchangeTab(val) {
            this.queryUrlList()
        },
        //
        //
        tableSortChange(column) {

            this.currentPage = 1
            if (column.order == "descending") {
                this.order = 1
            } else {
                this.order = 0
            }
            this.queryUrlList()

        },
        //
        goLink() {
            this.$router.push({
                path: "/back_source"
            })
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        //
        onImport() {
            this.$router.push({
                path: "/upload"
            })

        },

        //

        exportExcel() {
            require.ensure([], () => {
                const {
                    export_json_to_excel
                } = require("../../excel/Export2Excel");
                const tHeader = [

                    "URL",
                    "",
                    "ID",
                    "",
                    "",
                    "",

                ];
                // Excel
                const filterVal = [

                    "url",
                    "url_name",
                    "buser_id",
                    "state",
                    "create_time",
                    "label",

                ];
                // indexnickNamenametableData
                const list = this.tableData2; //datatableDatalist
                const data = this.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, "");
            });
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },
        // 
        onSubmitInput() {
            var rx = /^http?:\/\//i;
            if (rx.test(this.input)) {
                this.nowurl = this.input
            } else {
                this.buser_id = this.input
            }
            this.queryUrlList()
        },
        //
        formatState(rows) {
            if (rows.state == 1) {
                return ""
            } else {
                return ""
            }
            //return jsonData.map(v => filterVal.map(j => v[j]));
        },
        formatTime(rows) {
            let tempTime = (rows.create_time) * 1000
            return this.common.getTimes(tempTime)

        },
        //
        next() {
            if (this.actives == 2) {
                const confirmText = [
                    "",
                    ""
                ];
                const newDatas = [];
                const h = this.$createElement;
                for (const i in confirmText) {
                    newDatas.push(h("p", null, confirmText[i]));
                }

                this.$confirm("", {
                        title: "",
                        message: h("div", null, newDatas),
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    })
                    .then(() => {
                        this.dialupdata = false;
                        this.actives = 1;
                        // 
                        this.$message({
                            type: "success",
                            message: "!"
                        });
                    })
                    .catch(() => {
                        this.dialupdata = false;
                        this.actives = 1;
                        this.$message({
                            type: "info",
                            message: ""
                        });
                    });
            }
            if (this.actives++ > 2) {
                if (this.active++ > 2) this.active = 0;
                return false;
            }
        },
        //
        last() {
            if (this.actives-- < 1) {
                // this.actives = 0;
                return false;
            }
        },
        //
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        //
        fureset() {
            this.actives = 1;
            this.dialupdata = false;
        },
        //
        // Configuration(row) {
        //     console.log(row)
        //     return  false
        //     this.$router.push({
        //         path: "/back_source",
        //         query:{
        //             param:""
        //         }
        //     })
        // },
        new_btn() {

        },
        //URL
        onSubmitAdd() {
            this.dialogFormVisible = false
            let param = new Object
            param = this.dynamicValidateForm
            add_url(param).then(res => {


                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: ""
                    });
                    this.common.monitoringLogs('', 'URL', 1)

                    this.queryUrlList()
                } else {
                    this.common.monitoringLogs(' ', '', 0)

                }

            }).catch(error => {
            })

        },
        //-URL
        removeDomain(item) {
            var index = this.dynamicValidateForm.domains.indexOf(item);
            if (index !== -1) {
                this.dynamicValidateForm.domains.splice(index, 1);
            }
        },
        //-URL
        addDomain() {
            this.dynamicValidateForm.domains.push({
                value: "",
                key: Date.now()
            });
        },
        //-
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        //
        setdomainlist() {
            let tempUrlArr = []
            tempUrlArr = this.tableData
            localStorage.setItem('tempUrlArr', JSON.stringify(tempUrlArr))

            this.$router.push({
                path: "/batch_management"
            })
        },
        //URL
        queryUrlList() {

            let params = new Object();
            params.page = this.currentPage - 1
            params.buser_id = this.buser_id
            params.url = this.nowurl
            params.order = this.order
            if (this.value === "") {
                params.state = -1
            } else {
                params.state = this.value
            }
            if (this.value1) {
                params.end_time = this.value1[1].getTime() / 1000
                params.start_time = this.value1[0].getTime() / 1000
            } else {

            }
            //params.state=0
            // params.end_time=123456
            //  params.start_time=23456
            query_url(params)
                .then(res => {
                    if (res.status == 0) {
                        let tempArr = []
                        tempArr = res.data.result
                        tempArr.forEach((item, index) => {
                            item.create_time = this.common.getTimes(item.create_time * 1000)

                        });
                        this.tableData = tempArr

                        this.total_cnt = res.data.total
                    } else {

                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //
        toexportExcel() {

            let params = new Object();
            params.page = this.currentPage - 1
            params.buser_id = this.buser_id
            params.url = this.nowurl
            params.order = this.order
            if (this.value === "") {
                params.state = -1
            } else {
                params.state = this.value
            }
            if (this.value1) {
                params.end_time = this.value1[1].getTime() / 1000
                params.start_time = this.value1[0].getTime() / 1000
            } else {

            }
            query_url(params)
                .then(res => {
                    if (res.status == 0) {
                        let tempArr = []
                        tempArr = res.data.result
                        for (var i = 0; i < tempArr.length; i++) {
                            tempArr[i].create_time = this.common.getTimes(tempArr[i].create_time * 1000)
                            if (tempArr[i].state == 1) {
                                tempArr[i].state = ""
                            } else {
                                tempArr[i].state = ""
                            }
                        }
                        if (this.pageActive >= Math.ceil(res.data.total / 10)) {
                            this.exportExcel()
                            this.common.monitoringLogs('', 'URL', 1)

                        } else {
                            this.tableData2 = this.tableData2.concat(tempArr);

                            this.pageActive++
                            this.toexportExcel()

                        }

                        this.tableData = res.data.result
                        this.total_cnt = res.data.total

                    } else {
                        this.common.monitoringLogs('', 'URL', 0)
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //
        handleCurrentChange(pages) {
            this.currentPage = pages;
            this.queryUrlList()
            //this.getdata();
        },
        //
        handleSizeChange(pagetol) {
            this.currentPage = pagetol;
            //this.getdata();
        },
        //
        onSubmit() {
        },
        //
        Configuration(val) {
            let tempUrl = val.url
            let tempChanId = val.buser_id
            localStorage.setItem('tempUrl', tempUrl)
            localStorage.setItem('tempChanId', tempChanId)
            this.$router.push({
                path: "/back_source",
            })
        },
        //
        option_display() {
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //
        seachuser() {
            var rx = /^http?:\/\//i;
            if (rx.test(this.input)) {
                this.nowurl = this.input
            } else {
                this.buser_id = this.input
            }
            this.queryUrlList()
            //this.value1 = "";

        },
        //
        reset() {
            this.value = "";
            this.value1 = null;
            this.input = "";
            this.buser_id = ""
            this.nowurl = ""
            this.queryUrlList()
        },
        //
        addUrl() {
            this.$router.push({
                path: "/add_url"
            })
            this.dialogFormVisible = true;
        },
        //
        dialogFormVisibles() {
            this.radio = "1";
            this.dynamicValidateForm.account = "";
            this.dynamicValidateForm.nickname = "";
            this.dynamicValidateForm.pwd = "";
            this.dynamicValidateForm.conpwd = "";
            this.dynamicValidateForm.actualname = "";
            this.dynamicValidateForm.tel = "";
            this.dialogFormVisible = false;
        },
        //
        dialogFormVisiblea() {
            console.log(
                this.dynamicValidateForm.name,
                this.dynamicValidateForm.region
            );
            this.dynamicValidateForm.name = "";
            this.dynamicValidateForm.region = "";
            this.dialogFormVisible = false;
        },
        //
        handleClick(row) {
            this.ruleForm4 = row
            this.dialogVisible4 = true
            if (this.ruleForm4.status == 0) {
                this.ruleForm4.status = ""
            } else {
                this.ruleForm4.status = ""
            }

        },
        //
        updatauser(val) {
            let tempUrl = val.url
            let tempChanId = val.buser_id
            localStorage.setItem('tempUrl', tempUrl)
            localStorage.setItem('tempChanId', tempChanId)
            if (val.radio == "") {
                this.radioes = "1";
            } else {
                this.radioes = "2";
            }
            this.uetails = val;
            this.$router.push({
                path: "/copy",
                query:{
                    linKUrl:val.url
                }
            })
            // this.dialupdata = true;
        },
        //
        updataa() {
            this.uetails = {};
            this.dialupdata = false;
        },
        //
        updatab() {
            this.uetails = {};
            this.dialupdata = false;
        },
        //
        onDisable(row) {
            this.$confirm("", "", {
                type: "warning"
            }).then(() => {
                if (row.state == 0) {
                    //
                    let tempArr = []
                    let tempArr1 = []
                    tempArr[0] = row.url
                    tempArr[1] = 1
                    tempArr1.push(tempArr)
                    let param = {
                        data_count: 1,
                        data_array: tempArr1
                    }
                    change_state(param).then(res => {
                        if (res.status == 0) {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.queryUrlList()
                            this.common.monitoringLogs(' ', 'URL', 1)

                        } else {
                            this.common.monitoringLogs(' ', 'URL', 0)

                        }

                    }).catch(error => {
                        console.log(error)
                    })
                } else if (row.state == 1) {
                    //
                    let tempArr = []
                    let tempArr1 = []
                    tempArr[0] = row.url
                    tempArr[1] = 0
                    tempArr1.push(tempArr)
                    let param = {
                        data_count: 1,
                        data_array: tempArr1
                    }
                    change_state(param).then(res => {
                        if (res.status == 0) {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                            this.common.monitoringLogs(' ', 'URL', 1)
                            this.queryUrlList()
                        } else {
                            this.common.monitoringLogs(' ', 'URL', 0)

                        }

                    }).catch(error => {
                        console.log(error)
                    })
                }

            }).catch(() => {

            })

            // this.ruleForm.account = row.account;
            // this.dialpwdset = true;
        },
        submitForm(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.$message({
                        message: "",
                        type: "success"
                    });
                    this.dialpwdset = false;
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialpwdset = false;
        },
        // 
        // disableuser() {
        //     this.$confirm(", ?", "", {
        //             confirmButtonText: "",
        //             cancelButtonText: "",
        //             type: "warning"
        //         })
        //         .then(() => {
        //             this.$message({
        //                 type: "success",
        //                 message: "!"
        //             });
        //         })
        //         .catch(() => {
        //             this.$message({
        //                 type: "info",
        //                 message: ""
        //             });
        //         });
        // },
        //
        enableuser() {
            let tempArr = this.multipleSelection

            let tempArr2 = []
            for (var i = 0; i < tempArr.length; i++) {
                let tempArr1 = []
                tempArr1[0] = tempArr[i].url;
                tempArr1[1] = 1;
                tempArr2.push(tempArr1)
            }

            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr2
            change_state(param).then(res => {
                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                    this.queryUrlList()
                    this.common.monitoringLogs(' ', 'URL', 1)
                } else {
                    this.common.monitoringLogs(' ', 'URL', 0)

                }
            }).catch(error => {
                console.logA(error)
            })

        },
        //
        disableuser() {
            let tempArr = this.multipleSelection
            let tempArr1 = []
            let tempArr2 = []
            for (var i = 0; i < tempArr.length; i++) {
                let tempArr1 = []
                tempArr1[0] = tempArr[i].url;
                tempArr1[1] = 0;
                tempArr2.push(tempArr1)
            }

            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr2
            change_state(param).then(res => {
                if (res.status == 0) {
                    this.$message({
                        type: "success",
                        message: "!"
                    });
                    this.queryUrlList()
                    this.common.monitoringLogs(' ', 'URL', 1)

                } else {
                    this.common.monitoringLogs(' ', 'URL', 0)

                }
            }).catch(error => {
                console.logA(error)
            })

        },

        // 
        deleateuser(rows) {
            let tempArr = this.multipleSelection
            let tempArr1 = []
            let tempArr2 = []

            for (var i = 0; i < tempArr.length; i++) {
                if (tempArr[i].state == 1) {
                    this.$message({
                        type: "warning",
                        message: "URL!"
                    });
                    return false
                }
                tempArr1.push(tempArr[i].url)

            }
            let param = new Object()
            param.data_count = tempArr.length
            param.data_array = tempArr1
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    delete_url(param).then(res => {
                        if (res.status == 0) {
                            this.$message({
                                type: "success",
                                message: "!"
                            });
                            this.queryUrlList()
                            this.common.monitoringLogs(' ', 'URL', 1)

                        } else {
                            this.common.monitoringLogs(' ', 'URL', 0)

                        }
                    }).catch(error => {

                    })

                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        }, // 
        // 
        deleateuser1(rows) {

            if (rows.state == 1) {
                this.$message({
                    type: "warning",
                    message: "URL!"
                });
                return false
            }

            let tempArr1 = []
            tempArr1.push(rows.url)

            let param = new Object()
            param.data_count = 1
            param.data_array = tempArr1
            this.$confirm(", ?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                .then(() => {
                    delete_url(param).then(res => {
                        if (res.status == 0) {
                            this.$message({
                                type: "success",
                                message: "!"
                            });
                            this.queryUrlList()
                            this.common.monitoringLogs(' ', 'URL', 1)

                        } else {
                            this.common.monitoringLogs(' ', 'URL', 0)

                        }
                    }).catch(error => {

                    })

                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        }, // 
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

