<template>
<div class="content">
    <!--  -->
    <el-breadcrumb separator="/">
        <el-breadcrumb-item> </el-breadcrumb-item>
        <!-- <el-breadcrumb-item>
        <a href="/">ip</a>
      </el-breadcrumb-item> -->
    </el-breadcrumb>
    <!--  -->
    <div class="seach">
        <div class="seach_top">
            <el-input placeholder="IP" v-model="input" class="input-with-select" @keyup.enter.native="onSubmitKey" maxlength="70">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
            <el-input placeholder="ID" v-model="inputActive1" class="input-with-select" @keyup.enter.native="onSubmitKey" maxlength="70" style="margin-left:20px;">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
            <el-input placeholder="URL" v-model="inputActive2" class="input-with-select" @keyup.enter.native="onSubmitKey" maxlength="70" style="margin-left:20px;">
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
            <div class="seach_top_right" @click="option_display()">
                
                <i class="el-icon-caret-bottom" :class="[rotate?'fa fa-arrow-down go':'fa fa-arrow-down aa']"></i>
            </div>
        </div>
        <div v-if="optiondisplay" class="seach_bottom">
            <span></span>
            <el-date-picker v-model="value1" type="datetimerange" range-separator="" :picker-options="pickerOptions" start-placeholder="" end-placeholder=""></el-date-picker>
            <div class="seach_bottom_btn">
                <el-button type="primary" plain size="mini" @click="seachuser()"></el-button>
                <el-button plain size="mini" @click="reset()"></el-button>
            </div>
        </div>
    </div>

    <!--  -->
    <div style="background: #fff;margin-top:10px;padding:20px;">
        <div style="text-align:right;padding-bottom:10px;">
            <el-button type="primary" @click="toexportExcelNew()"></el-button>

        </div>
        <el-table stripe :data="tableData" :cell-style="rowClass" :header-cell-style="headClass" style="width: 100%">
            <el-table-column prop="ipInfo" label="IP" width="180"></el-table-column>
            <el-table-column prop="chanId" label="ID" width="180"></el-table-column>
            <el-table-column prop="fileUrl" label="URL"></el-table-column>
            <el-table-column prop="dataFlow" label=""></el-table-column>
            <el-table-column prop="accessCnt" label=""></el-table-column>
            <el-table-column prop="failCnt" label=""></el-table-column>
            <el-table-column prop="startTS" label=""></el-table-column>
            <el-table-column prop="endTS" label=""></el-table-column>
            <el-table-column prop="totalTime" label=""></el-table-column>
        </el-table>
    </div>
    <fenye style="float:right;margin:10px 0 0 0;" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :currentPage="currentPage" :pagesa="total_cnt"></fenye>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    query_accelerate_log,
    export_videoaccel_file
} from "../../servers/api";
import common from "../../comm/js/util";
export default {
    data() {
        return {
            rotate: false,
            errTableVisible: false,
            device_show: false,
            currentPage: 1,
            inputActive1: "",
            inputActive2: "",
            total_cnt: 0,
            value1: "",
            input: "",
            value: "",
            input1: "", //
            input2: "", //
            optiondisplay: false,
            dev_devation: {},
            sizeForm: {
                date1: "",
                date2: ""
            },
            options: [{
                    value: 0,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                },
                {
                    value: 1,
                    label: ""
                }
            ],
            tableData: [],
            tableData2: [],
            pageActive: 0,
            fileUrl: "",
            userIp: "",
            chanId: "",
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now() - 8.64e6; //-8.64e6
                },
            }
        };
    },
    components: {
        fenye
    },
    mounted() {
        this.queryInfo();
    },
    methods: {
        //
        toexportExcelNew() {
            let startTime = 0;
            let endTime = 0;
            if (this.value1) {
                if (this.value1 == "") {
                    startTime = 1571465783;
                    endTime = 1576736183;
                } else {
                    endTime = this.value1[1].getTime() / 1000;
                    startTime = this.value1[0].getTime() / 1000;
                }
            } else {
                startTime = 1571465783;
                endTime = 1576736183;
            }

            let param = {
                start_ts: startTime,
                end_ts: endTime,
                chanId: this.inputActive1 == "" ? "*" : this.inputActive1,
                fileUrl: this.inputActive2 == "" ? "*" : this.inputActive2,
                userIp: this.input == "" ? "*" : this.input,
            }
            export_videoaccel_file(param).then(res => {
                if (res.status == 0) {
                    this.$message({
                        message: "",
                        type: "success"
                    })
                    window.location.href = res.msg

                }

            }).catch(error => {

            })

        },
        //

        //
        formatterTime(row) {
            return this.common.getTimes(row.startTS * 1000);
        },
        //
        formatterTime1(row) {
            return this.common.getTimes(row.endTS * 1000);
        },
        //
        queryInfo() {
            let startTime = 0;
            let endTime = 0;
            if (this.value1) {
                if (this.value1 == "") {
                    startTime = 1571465783;
                    endTime = 1576736183;
                } else {
                    endTime = this.value1[1].getTime() / 1000;
                    startTime = this.value1[0].getTime() / 1000;
                }
            } else {
                startTime = 1571465783;
                endTime = 1576736183;
            }

            let param = {
                start_ts: startTime,
                end_ts: endTime,
                chanId: this.inputActive1 == "" ? "*" : this.inputActive1,
                fileUrl: this.inputActive2 == "" ? "*" : this.inputActive2,
                userIp: this.input == "" ? "*" : this.input,
                pageNo: this.currentPage - 1,
                pageSize: 10
            };
            query_accelerate_log(param)
                .then(res => {
                    if (res.status == 0) {
                        let tempArr = res.data.list;
                        for (var i = 0; i < tempArr.length; i++) {
                            tempArr[i].startTS = this.common.getTimes(
                                tempArr[i].startTS * 1000
                            );
                            tempArr[i].endTS = this.common.getTimes(tempArr[i].endTS * 1000);
                        }
                        this.tableData = tempArr;
                        this.total_cnt = res.data.totalCnt;
                    }
                })
                .catch(error => {});
        },
        //

        exportExcel() {
            require.ensure([], () => {
                const {
                    export_json_to_excel
                } = require("../../excel/Export2Excel");
                const tHeader = [
                    "IP",
                    "ID",
                    "URL",
                    "",
                    "",
                    "",
                    "IP",
                    "",
                    ""
                ];
                // Excel
                const filterVal = [
                    "ipInfo",
                    "chanId",
                    "fileUrl",
                    "dataFlow",
                    "accessCnt",
                    "failCnt",
                    "startTS",
                    "endTS",
                    "totalTime"
                ];
                // indexnickNamenametableData
                const list = this.tableData2; //datatableDatalist
                const data = this.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, "");
            });
        },
        formatJson(filterVal, jsonData) {
            return jsonData.map(v => filterVal.map(j => v[j]));
        },
        toexportExcel() {
            let startTime = 0;
            let endTime = 0;
            if (this.value1) {
                if (this.value1 == "") {
                    startTime = 1571465783;
                    endTime = 1576736183;
                } else {
                    endTime = this.value1[1].getTime() / 1000;
                    startTime = this.value1[0].getTime() / 1000;
                }
            } else {
                startTime = 1571465783;
                endTime = 1576736183;
            }

            let param = {
                start_ts: startTime,
                end_ts: endTime,
                chanId: this.inputActive1 == "" ? "*" : this.inputActive,
                fileUrl: this.inputActive2 == "" ? "*" : this.inputActive2,
                userIp: this.input == "" ? "*" : this.input,
                pageNo: this.pageActive,
                pageSize: 10
            };
            query_accelerate_log(param)
                .then(res => {
                    if (res.status == 0) {
                        if (this.pageActive >= res.data.totalPage) {
                            this.exportExcel();
                            this.common.monitoringLogs("", "", 1);
                        } else {
                            let tempArr = res.data.list;
                            for (var i = 0; i < tempArr.length; i++) {
                                tempArr[i].startTS = this.common.getTimes(
                                    tempArr[i].startTS * 1000
                                );
                                tempArr[i].endTS = this.common.getTimes(
                                    tempArr[i].endTS * 1000
                                );
                            }
                            this.tableData2 = this.tableData2.concat(tempArr);
                            this.pageActive++;
                            this.toexportExcel();
                        }

                        this.total_cnt = res.data.totalCnt;
                    } else {
                        this.common.monitoringLogs("", "", 0);
                    }
                })
                .catch(error => {});
        },

        //
        option_display() {
            this.optiondisplay = !this.optiondisplay;
            this.rotate = !this.rotate;
        },
        //
        seachuser() {
            // this.value = "";
            // this.input = "";

            // this.value = "";
            // this.input = "";
            this.queryInfo();
        },
        //
        onSubmitKey() {
            this.currentPage = 1;
            this.queryInfo();
        },
        //
        reset() {
            this.input = ""
            this.value1 = ""
            this.currentPage = 1
            this.inputActive1 = ""
            this.inputActive2 = ""

            this.queryInfo();
        },
        // 
        headClass() {
            return "text-align: center;background:#eef1f6;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        },
        //
        handleCurrentChange(pages) {
            this.currentPage = pages;
            this.queryInfo();
            //this.getdata();
        },
        //
        handleSizeChange(pagetol) {
            this.currentPage = pagetol;
            //this.getdata();
        }
    }
};
</script>

<style lang="scss" scoped>
.content {
    .device_title_name {
        width: 16%;
        background: #eeeeee;
        color: #000000;
    }

    .device_title_content {
        width: 16%;
        background: #ffffff;
        color: #000000;
    }

    .tableclose {
        width: 100%;
        padding-top: 10px;
        text-align: center;
    }
}

//
.aa {
    transition: all 1s;
}

.go {
    transform: rotate(-180deg);
    transition: all 1s;
}
</style>



