<template>
<div class="copy_configuration">
    <div class="top_title" style="display: flex;">
        <span @click="goback" style="font-size: 24px;color: #202020;"><i class="el-icon-arrow-left" style="color:#297AFF;font-size: 18px;margin-right:23px;font-weight: 600;"></i>
            </span>
    </div>
    <p class="copy_title">
        URLURLURL
    </p>
    <div class="copy_con">
        <div>
            <el-steps :active="actives" finish-status="success" simple style="margin-top: 20px;width:600px;">
                <el-step title="" description=""></el-step>
                <el-step title="URL" description=""></el-step>
                <el-step title="" description=""></el-step>
            </el-steps>
        </div>

        <div v-if="actives === 1">
            <el-table ref="multipleTable" :data="futableData" tooltip-effect="dark" stripe style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="configuration" label=""></el-table-column>
                <el-table-column prop="nowconfiguration" label="" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <div v-if="scope.row.nowconfiguration == ''" style="display: flex;justify-content: center;align-items: center;color:#333333;">
                            <i class="iconfont icon-dian" style="font-size: 36px;color:#297AFF;"></i><span> {{ scope.row.nowconfiguration }}</span>
                        </div>
                        <div v-else style="display: flex;justify-content: center;align-items: center;color:#949494;">
                            <i class="iconfont icon-dian" style="font-size: 36px;color:#828282;"></i><span> {{ scope.row.nowconfiguration }}</span>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column>
                    <template slot-scope="scope" label="">
                        <el-button @click="handleClick(scope.row)" type="text" size="small" style="color:#547DC0;" v-if="scope.row.nowconfiguration == ''"></el-button>
                        <el-button @click="handleClick(scope.row)" type="text" size="small" v-else></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button style="margin-top: 12px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 12px;" @click="fureset"></el-button>
        </div>
        <div v-else-if="actives === 2">
            <div style="display: flex;justify-content: flex-start;align-items: center;margin: 10px 0">
                <span style="margin-right:21px;color:#333333;font-size:14px;">URL</span>
                <el-input placeholder="URL" v-model="fuinput" style=" width:300px" class="input-with-select" maxlength="70" @keyup.enter.native="onSubmit"></el-input>
            </div>
            <div class="copy_prompt">
                <div class="banyuan"></div>
                <i class="el-icon-info" style="color:#6666FF;font-size:18px;margin-left:15px;"></i>
                
                <span>{{ urlnum }}</span>URL50
                <span @click="seturllist()" v-if="urlnum !== 0"></span>
            </div>
            <!--  -->
            <div class="copy_active2">
                <el-dialog title="url" :visible.sync="urllistVisible">
                    <el-table :data="urldisable" :cell-style="rowClass" :header-cell-style="headClass">
                        <el-table-column type="index" label="" width="150">
                        </el-table-column>
                        <el-table-column label="url">
                            <template slot-scope="scope">
                                <div style="width: 700px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;margin:0 auto;"> {{scope.row}}</div>
                            </template>
                        </el-table-column>
                    </el-table>

                    <div slot="footer" class="dialog-footer">
                        <el-button @click="urllistVisible = false" type="primary"> </el-button>
                    </div>
                </el-dialog>
            </div>
            <!--  -->
            <div>
                <el-table ref="multipleTable" :row-key="getRowKey" :data="urllist" tooltip-effect="dark" stripe style="width: 100%" :cell-style="rowClass" :header-cell-style="headClass" @selection-change="handlistChange">
                    <el-table-column type="selection" width="55" :reserve-selection="true"></el-table-column>
                    <el-table-column label="URL">
                        <template slot-scope="scope">
                            <div style="width: 700px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;margin:0 auto;">{{scope.row.url}}</div>

                        </template>
                    </el-table-column>
                </el-table>
                <fenye style="text-align:right;margin:20px 0 10px 0;" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange" :pagesa="total_cnt"></fenye>
            </div>
            <el-button style="margin-top: 2px;" type="primary" @click="last"></el-button>
            <el-button style="margin-top: 2px;" type="primary" @click="next"></el-button>
            <el-button style="margin-top: 2px;" @click="fureset"></el-button>
        </div>
    </div>
</div>
</template>

<script>
import fenye from "@/components/fenye";
import {
    query_url,
    config_url,
    query_urllabel
} from "../../servers/api";
import {
    dateToMs,
    getymdtime
} from "../../servers/sevdate";
export default {
    data() {
        return {
            actives: 1,
            fuinput: "",
            urlname: "",
            urllistVisible: false,
            urllist: [],
            urlnum: 0,
            tolpage: 1,
            pagesize: 10,
            total_cnt: 1,
            chanid: "",
            urldisable: [],
            futableData: [{
                    configuration: "HOST",
                    nowconfiguration: "",
                    pname: "host_url",
                    tabnum: "two"
                },
                {
                    configuration: "",
                    nowconfiguration: "",
                    pname: "cache_config",
                    tabnum: "there"
                },
                {
                    configuration: "",
                    nowconfiguration: "",
                    pname: "expire_time",
                    tabnum: "second"
                },
                {
                    configuration: "",
                    nowconfiguration: "",
                    pname: "custom_page",
                    tabnum: "four"
                }
            ],
            //
            datalist: {
                url: "",
                label: "",
                create_time: "",
                label2: 0,
                host_url: {
                    valid: 0,
                    url: ""
                },
                cache_config: {
                    // val: "",
                    valid: 0,
                    expire_time: 0,
                    weight: 0
                },
                band_config: {
                    valid: 0,
                    top_band: 0
                },
                custom_page: []
            },
            urlLinks: "",
            tempChanId: "",

            copydatalist: {},
            multipleSelection: [],
            configuration: {}, //,
            tabIndex: "",
        };
    },
    components: {
        fenye
    },
    mounted() {
        let tempUrl = localStorage.getItem("tempUrl");
        if (tempUrl) {
            this.urlLinks = tempUrl;
        } else {
            this.urlLinks = "";
        }
        let tempChanId = localStorage.getItem("tempChanId");
        if (tempChanId) {
            this.buser_ids = tempChanId;
        } else {
            this.buser_ids = "";
        }
        // if (this.$cookies.get("id")) {
        //     this.chanid = this.$cookies.get("id") * 1;
        // } else {
        //     this.$router.push({
        //         path: "/"
        //     });
        // }
        this.urlname = this.$route.query.urlress;
        this.geturlconfig();
        this.geturllist();
    },
    methods: {
        getRowKey(row) {
            return row.url
        },
        // ,val
        handleSelectionChange(val) {
            this.urldisable = val
        },
        // 
        clearSelect() {
            this.$refs.buyerTable.clearSelection()
        },

        //
        handleCurrentChange(pages) {
            this.tolpage = pages;
            this.geturllist();
        },
        //
        handleSizeChange(pagetol) {
            this.pagesize = pagetol;
            // this.geturllist();
        },
        //url
        geturlconfig() {
            let params = new Object();
            params.page = 0;
            params.buser_id = this.tempChanId + "";
            params.url = this.urlLinks;
            params.state = "";
            params.start_time = "";
            params.end_time = "";
            params.order = 0;
            query_url(params)
                .then(res => {
                    if (res.status == 0) {
                        this.copydatalist = res.data.result[0];
                        //
                        if (res.data.result[0].custom_page.length <= 0) {
                            this.futableData[3].nowconfiguration = "";
                        } else {
                            this.futableData[3].nowconfiguration = "";
                        }
                        //
                        if (res.data.result[0].cache_config.valid == 0) {
                            this.futableData[1].nowconfiguration = "";
                        } else {
                            this.futableData[1].nowconfiguration = "";
                        }
                        //--
                        if (res.data.result[0].cache_config.data.length <= 0) {
                            this.futableData[2].nowconfiguration = "";
                        } else {
                            this.futableData[2].nowconfiguration = "";
                        }
                        //
                        if (res.data.result[0].host_url.url == "") {
                            this.futableData[0].nowconfiguration = "";
                        } else {
                            this.futableData[0].nowconfiguration = "";
                        }
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //url
        geturllist() {
            this.urllist = [];
            let params = new Object();
            params.page = this.tolpage - 1;
            params.buser_id = this.chanid + "";
            params.url = this.fuinput;
            params.state = "";
            params.start_time = "";
            params.end_time = "";
            params.order = 0;
            query_url(params)
                .then(res => {
                    if (res.status == 0) {
                        res.data.result.forEach((item, index) => {
                            let urlobj = {};
                            urlobj.url = item.url;
                            this.urllist.push(urlobj);
                            this.total_cnt = res.data.total;
                        });
                    }
                })
                .catch(err => {
                    console.log(err);
                });
        },
        //
        onSubmit() {
            this.geturllist();
        },
        //
        seturlconfig() {
            this.multipleSelection.forEach((item, index) => {
                if (item == "host_url") {
                    this.configuration[item] = JSON.parse(
                        JSON.stringify(this.copydatalist[item])
                    );
                } else if (item == "cache_config") {
                    let dataarr = {};
                    dataarr.cache_config = {};
                    dataarr.cache_config.data = this.copydatalist.cache_config.data;
                    dataarr.cache_config.valid = this.copydatalist.cache_config.valid;
                    this.configuration.cache_config = dataarr;
                } else if (item == "expire_time") {
                    this.configuration.cache_config = JSON.parse(
                        JSON.stringify(this.copydatalist.cache_config)
                    );
                } else if (item == "custom_page") {
                    this.configuration[item] = JSON.parse(
                        JSON.stringify(this.copydatalist[item])
                    );
                }
            });
        },
        //
        copyconfig() {
            let params = new Object();
            params.data_count = this.urldisable.length;
            params.data_array = [];
            this.urldisable.forEach((item, index) => {
                var jsonobj = new Object();
                jsonobj = Object.assign({}, this.configuration);
                jsonobj.url = item;
                jsonobj.label = this.copydatalist.label;
                jsonobj.label2 = this.copydatalist.label2;
                jsonobj.buser_id = this.copydatalist.buser_id;
                jsonobj.url_type = this.copydatalist.url_type;
                jsonobj.url_name = this.copydatalist.url_name;
                jsonobj.create_time = this.copydatalist.create_time;
                params.data_array.push(jsonobj);
            });
            config_url(params)
                .then(res => {
                    if (res.status == 0) {
                        if (res.data.failed_count == 0) {
                            this.$message({
                                type: "success",
                                message: "!"
                            });
                        } else {
                            let errarr = "";
                            res.data.res_data.forEach((item, index) => {
                                errarr = errarr.concat(item[0] + "</br>");
                            });
                            this.$alert(
                                `${errarr}`,
                                res.data.failed_count + "", {
                                    dangerouslyUseHTMLString: true
                                }
                            );
                        }
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        },
        //
        next() {
            if (this.actives == 1) {
                if (this.multipleSelection.length <= 0) {
                    this.$message.error("");
                    return false;
                } else {
                    this.seturlconfig();
                }
            }
            if (this.actives == 2) {
                if (this.urldisable.length <= 0) {
                    this.$message.error("URL");
                    return false;
                }
                if (this.urldisable.length > 50) {
                    this.$message.error("50URL");
                    return false;
                }
                const confirmText = [
                    "URLURLURL"
                ];
                const newDatas = [];
                const h = this.$createElement;
                for (const i in confirmText) {
                    newDatas.push(h("p", null, confirmText[i]));
                }

                this.$confirm("", {
                        title: "",
                        message: h("div", null, newDatas),
                        showCancelButton: true,
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    })
                    .then(() => {
                        this.copyconfig();
                        this.dialupdata = false;
                        this.actives = 1;
                        // 
                    })
                    .catch(() => {
                        this.dialupdata = false;
                        this.actives = 2;
                        this.$message({
                            type: "info",
                            message: ""
                        });
                    });
            }
            if (this.actives++ > 2) {
                if (this.active++ > 2) this.active = 0;
                return false;
            }
        },
        //
        last() {
            if (this.actives-- < 1) {
                // this.actives = 0;
                return false;
            }
        },
        //
        handleSelectionChange(val) {
            if (val) {
                val.forEach((item, index) => {
                    this.multipleSelection.push(item.pname);
                });
            }
        },
        //URL--
        handlistChange(val) {
            if (val) {
                this.urlnum = val.length;
                this.urldisable = [];
                val.forEach((item, index) => {
                    this.urldisable.push(item.url);
                });
            } else {

            }
        },
        //--
        handleClick(confignum) {
            //   let tempUrl = val.url
            //let tabIndex = confignum.tabnum
            // localStorage.setItem('tabIndex', tabIndex)
            // localStorage.setItem('tempChanId', tempChanId)
            this.$router.push({
                path: "/back_source",
                query: {
                    urlress: this.urlname,
                    tabpne: confignum.tabnum
                }
            });
        },
        //url
        seturllist() {
            this.urllistVisible = true;
        },
        //
        fureset() {
            this.actives = 1;
            this.dialupdata = false;
        },
        //
        goback() {
            this.$router.go(-1);
        },
        // 
        headClass() {
            return "text-align: center;background:#eef1f6;color:#333333;font-size:16px;";
        },
        // 
        rowClass() {
            return "text-align: center;";
        }
    }
};
</script>

<style lang="scss" scoped>
.copy_configuration {
    .copy_title {
        width: 1240px;
        height: 40px;
        background: rgba(224, 240, 255, 1);
        border: 1px solid rgba(97, 157, 255, 1);
        font-size: 14px;
        color: #728abd;
        margin: 0 auto;
        margin-top: 20px;
        margin-bottom: 20px;
        line-height: 40px;
        text-align: left;
        padding-left: 17px;
    }

    .copy_con {
        width: 1240px;
        min-height: 675px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 2px 3px 0px rgba(6, 17, 36, 0.14);
        border-radius: 6px;
        margin: 0 auto;
        padding: 45px 37px;

        .copy_prompt {
            text-align: left;
            width: 632px;
            height: 50px;
            line-height: 50px;
            background: #f0f0ff;
            margin-bottom: 2px;
            border-radius: 2px;
            display: flex;
            align-items: center;
            border-radius: 2px;

            .banyuan {
                width: 5px;
                height: 100%;
                background: #6666ff;
                border-top-left-radius: 15px;
                border-bottom-left-radius: 15px;
            }

            span {
                font-size: 18px;
                color: #6666ff;
            }
        }
    }
}
</style>



