  filters: {
    settimes(data) {
      if (data) {
        var stat = getymdtime(data);
        return stat;
      } else {
        return data;
      }
    }
  },
  mounted() {
    let tempUrl = localStorage.getItem("tempUrl");
    if (tempUrl) {
      this.urlLinks = tempUrl;
    } else {
      this.urlLinks = "";
    }
    let tempChanId = localStorage.getItem("tempChanId");
    if (tempChanId) {
      this.buser_ids = tempChanId;
    } else {
      this.buser_ids = "";
    }
    let tabIndex = this.$route.query.tabpne;
    if (tabIndex) {
      this.oneName = tabIndex;
    } else {
      this.oneName = "one";
    }

    this.citylabel = ["", ""];
    // if (this.$cookies.get("id")) {
    //   this.chanid = this.$cookies.get("id") * 1;
    // } else {
    //   this.$router.push({ path: "/" });
    // }
    this.urlname = this.$route.query.urlress;
    if (this.$route.query.tabpne) {
      if (this.$route.query.tabpne == "second") {
        this.oneName = "there";
        this.activeName = "second";
      } else {
        this.oneName = this.$route.query.tabpne;
      }
    }
    this.label2 = [];
    this.options = [];
    this.geturlconfig();
    this.getlabrl2();
  },
  methods: {
    //url
    cleckurl() {
      this.zurl = true;
      this.sleckurl = this.datalist.url;
    },
    //
    getlabrl2() {
      let parmas = new Object();
      parmas.chanid = this.buser_ids;
      parmas.page = this.page;
      getterminal(parmas)
        .then(res => {
          res.result.cols.forEach((item, index) => {
            let obj = {};
            obj.value = item.id;
            obj.label = item.name + "--" + "(" + item.type + ")";
            this.label2.push(obj);
          });
          if (res.result.les_count == 0) {
            var obj = {};
            for (var i = 0; i < this.label2.length; i++) {
              if (!obj[this.label2[i].value]) {
                this.options.push(this.label2[i]);
                obj[this.label2[i].value] = true;
              }
            }
            return false;
          } else {
            this.page++;
            this.getlabrl2();
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //--
    geturlconfig() {
      let parmas = new Object();
      parmas.url = this.urlLinks;
      parmas.page = 0;
      parmas.buser_id = this.buser_ids + "";
      query_url(parmas)
        .then(res => {
          this.datalist.create_time = res.data.result[0].create_time;
          this.datalist.url = res.data.result[0].url;
          this.datalist.label = res.data.result[0].label;
          this.oldlabel = res.data.result[0].label;
          this.datalist.label2 = res.data.result[0].label2;
          this.datalist.state = res.data.result[0].state;
          //
          this.datalist.host_url.url = res.data.result[0].host_url.url;
          if (res.data.result[0].host_url.valid == 0) {
            this.datalist.host_url.valid = false;
            //
            this.valuek = false;
            this.urlno = false;
          } else {
            this.datalist.host_url.valid = true;
            this.valuek = true;
            this.urlno = true;
          }
          //
          let concash = {};
          if (res.data.result[0].cache_config.valid == 0) {
            //
            concash.valid = false;
            this.valueh = false;
          } else {
            concash.valid = true;
            this.valueh = true;
          }
          this.datalist.cache_con = res.data.result[0].cache_config.data;
          this.datalist.custom_page = res.data.result[0].custom_page;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //url
    setquery_url() {
      this.loading = false;
      if (this.datalist.label) {
        if (this.oldlabel !== this.datalist.label) {
          let adminlanel = new Object();
          let labelarr = [];
          labelarr.push(this.datalist.label);
          adminlanel.data_count = 1;
          adminlanel.data_array = labelarr;
          check_label(adminlanel)
            .then(res => {
              if (res.status == 0) {
                if (res.data.success_count == 0) {
                  this.$message.error("");
                  return false;
                } else {
                  this.updateurl();
                }
              }
              return false;
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          this.updateurl();
        }
      } else {
        this.updateurl();
      }
    },
    updateurl() {
      let parmas = new Object();
      let natobj = {};
      natobj.url = this.datalist.url;
      natobj.label = this.datalist.label;
      natobj.label2 = this.datalist.label2;
      natobj.host_url = new Object();
      natobj.cache_config = new Object();
      natobj.custom_page = [];
      if (this.valuek == false) {
        natobj.host_url.valid = 0;
      } else {
        natobj.host_url.valid = 1;
      }
      natobj.host_url.url = this.datalist.host_url.url;
      if (this.valueh == false) {
        natobj.cache_config.valid = 0;
      } else {
        natobj.cache_config.valid = 1;
      }
      if (this.valueh == false) {
        natobj.cache_config.valid = 0;
      } else {
        natobj.cache_config.valid = 1;
      }
      natobj.cache_config.data = this.datalist.cache_con;
      natobj.custom_page = this.datalist.custom_page;
      parmas.data_array = [];
      parmas.data_array.push(natobj);
      parmas.data_count = 1;
      config_url(parmas)
        .then(res => {
          this.geturlconfig();
          if (res.status == 0) {
            this.$message.success("");
            // setTimeout(() => {
            //   this.$router.push({ path: "/domain_management" });
            // }, 1000);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    disableuser(state) {
      let messagetext = "";
      if (state == 0) {
        messagetext = "URL, ?";
      } else {
        messagetext = "URL?";
      }
      this.$confirm(messagetext, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let params = new Object();
          let urllist = [];
          urllist.push(this.urlLinks);
          urllist.push(state);
          params.data_array = [];
          params.data_array.push(urllist);
          params.data_count = 0;
          change_state(params)
            .then(res => {
              if (res.status == 0) {
                this.geturlconfig();
                this.$message({
                  type: "success",
                  message: "!"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        })
        .catch(eerr => {
          console.log(eerr);
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //
    goback() {
      this.$router.go(-1);
    },
    //
    xsho() {
      this.xas = !this.xas;
      if (this.xas == false) {
        this.setquery_url();
      }
    },
    //
    fsho() {
      this.fas = !this.fas;
      if (this.fas == false) {
        this.setquery_url();
      }
    },
    //-
    xzurl() {
      this.dialogVisible = true;
      if (this.datalist.host_url.url) {
        this.huiurl.url = this.datalist.host_url.url;
      }
    },
    //-
    xfurl(data) {},
    //
    hosturl(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.datalist.host_url.url = this.huiurl.url;
          this.dialogVisible = false;
        }
        this.setquery_url();
      });
    },
    //--
    nohosturl(formName) {
      this.dialogVisible = false;
      //if (this.huiurl.url == "") {
      this.valuek = false;
      this.urlno = false;
      this.datalist.host_url.valid = false;
      //}

      this.$refs[formName].resetFields();
    },
    //
    handleClose1() {
      this.nohosturl("hosturlref");
    },
    //
    removeDomain(item) {
      var index = this.dynamicValidateForm.domains.indexOf(item);
      if (index !== -1) {
        this.dynamicValidateForm.domains.splice(index, 1);
      }
    },
    //
    addDomain() {
      this.dynamicValidateForm.domains.push({
        value: "",
        key: Date.now()
      });
    },
    handleClick(tab, event) {
      if (tab.name == "zero") {
        this.$router.push({
          path: "/domain_management"
        });
      }
    },
    ///
    zidingyi(dats) {
      if (this.datalist.custom_page[dats].state == 0) {
        this.datalist.custom_page[dats].state = 1;
      } else {
        this.datalist.custom_page[dats].state = 0;
      }
      this.setquery_url();
    },
    //
    delzi(dats) {
      this.datalist.custom_page.splice(dats, 1);
      this.setquery_url();
    },
    changeSwitch() {
      this.valuek = !this.valuek;
      if (this.valuek == true) {
        this.urlno = true;
        this.dialogVisible = true;
        if (this.datalist.host_url.url) {
          this.huiurl.url = this.datalist.host_url.url;
        }
      } else {
        this.urlno = false;
        this.dialogVisible = false;
        this.setquery_url();
      }
    },
    changeSwitch2() {
      this.setquery_url();
    },
    //-
    timestampToTime(timestamp) {
      var date = new Date(timestamp * 1000); //10*1000131000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //
    xhuanVisib(data, num) {
      this.city_disabled = true;
      this.citylabel = [data["area"], data["province"]];
      if (data["type"] == 1) {
        this.radio1 = "";
        this.automatic_time = false;
        this.huanfo = new Date(parseInt(data["expire"] * 1000, 10));
      } else {
        this.radio1 = "";
        this.automatic_time = true;
      }
      this.edit = 1;
      this.editnum = num;
      this.huanform = {};
      this.huanform.type = data["type"];
      this.huanform.area = data["area"];
      this.huanform.province = data["province"];
      this.huanform.expire = data["expire"];
      this.huanform.state = data["state"];
      this.huanVisible = true;
    },
    selecttime() {
      if (this.radio1 == "") {
        this.automatic();
      } else {
        this.customize();
      }
    },
    //--
    automatic() {
      this.automatic_time = true;
      this.huanform.type = 0;
      this.huanform.expire = 0;
      this.huanform.state = 0;
    },
    //--
    customize() {
      this.automatic_time = false;
      this.huanform.type = 1;
      this.huanform.state = 0;
      this.huanform.expire = dateToMs(this.huanfo);
    },
    //--
    huanVisib() {
      if (this.citylabel.length == 0) {
        this.$message({
          type: "error",
          message: ""
        });
        return false;
      }
      this.loading = true;
      if (this.huanform.type == 0) {
        this.huanform.expire = 0;
      } else {
        this.huanform.expire = dateToMs(this.huanfo);
        if (!this.huanform.expire) {
          this.$message({
            type: "error",
            message: ""
          });
          return false;
        }
      }
      this.huanform.area = this.citylabel[0];
      this.huanform.province = this.citylabel[1];

      if (this.edit == 1) {
        let temp = Object.assign({}, this.huanform);
        this.datalist.cache_con[this.editnum] = temp;
      } else {
        let temp = Object.assign({}, this.huanform);
        this.datalist.cache_con.push(temp);
      }

      this.huanVisible = false;
      this.citylabel = [];
      this.huanfo = "";
      this.automatic_time = true;
      setTimeout(() => {
        this.setquery_url();

        //this.reload();
      }, 1000);
    },
    //--
    huanno() {
      this.radio1 = "";
      this.huanfo = "";
      this.citylabel = [];
      this.huanVisible = false;
    },
    //
    delhc(data, num) {
      this.datalist.cache_con.splice(num, 1);
    },
    //--
    zareVisible(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let errobj = {};
          errobj.errCode = this.form.errCode;
          errobj.url = this.form.url;
          errobj.state = this.form.state;
          this.datalist.custom_page.push(errobj);
          this.dialogFormVisible = false;
          this.setquery_url();
          this.form.url = "";
          this.form.errCode = "";
        }
      });
    },
    //--
    zareno(formName) {
      this.form.url = "";
      this.form.errCode = "";
      this.$refs[formName].resetFields();
      this.dialogFormVisible = false;
    },
    //--
    handleClick4() {
      this.zareno("form");
    },
    //url
    jiourl(rule, value, callback) {
      if (value === "") {
        callback(new Error("URL"));
      } else {
        var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
        if (resyzm.test(value) === false) {
          callback(new Error("URL"));
        } else {
          callback();
        }
      }
    },
    //
    jioerrcode(rule, value, callback) {
      if (value == "") {
        callback(new Error(""));
      } else {
        var reserrcode = /^\d{3}$/;
        if (reserrcode.test(value) === false) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    },
    //
    option_display() {
      this.optiondisplay = !this.optiondisplay;
      this.rotate = !this.rotate;
    },
    //--
    onSubmit() {},
    //----
    seachuserhost() {},
    //----
    hostreset() {
      this.host_input = "";
      this.host_state = "";
      this.host_time = "";
    },
    //----
    hostgettimes(val) {},
    //
    gettimes() {},
    // 
    headClass() {
      return "text-align: center;background:#eef1f6;";
    },
    // 
    rowClass() {
      return "text-align: center;";
    }
  },
  beforeRouteLeave(to, form, next) {
    sessionStorage.removeItem("tabPosition");
    sessionStorage.removeItem("activeName");
    next();
  }
};
</script>

<style lang="scss" scoped>
.content {
  // width: 1240px;
  // margin: auto;
  // padding: 19px;
  // margin-top: 34px;
  // background: #ffffff;
  .bath_title_btn {
    font-size: 16px;

    i {
      font-size: 16px;
      font-weight: 600;
      margin-right: 5px;
    }
  }

  .tala_title {
    height: 50px;
    line-height: 50px;
    // background: #eeeeee;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    span {
      color: #333333;
      font-size: 18px;
      font-weight: 600;
    }
  }

  .tala {
    li {
      height: 50px;
      // line-height: 50px;
      text-align: left;

      span {
        margin-right: 25px;
      }

      .tala_con {
        display: inline-block;
        width: 200px;
      }
    }
  }

  .tala_x {
    margin-left: 10px;
    color: rgb(64, 158, 255);
  }

  .talb_title_tio {
    text-align: left;
    display: flex;

    span {
      margin-right: 45px;
    }
  }

  .talb_title_two {
    text-align: left;
    display: flex;
    margin: 15px 0;

    span {
      margin-right: 30px;
    }
  }
}

.seach {
  width: 100%;
  background: #ffffff;

  // margin: 30px 0 30px 0;
  // border-radius: 2px;
  // padding: 21px 37px;
  // box-shadow: 0px 0px 7px 0px rgba(41, 108, 171, 0.1);
  .seach_top {
    width: 100%;
    height: 60px;
    line-height: 60px;

    // display: flex;
    // align-items: center;
    .input-with-select {
      width: 20%;
      float: left;
    }

    .seach_top_right {
      float: left;
      width: 80px;
      text-align: center;
      height: 40px;
      margin-top: 10px;
      line-height: 40px;
      margin-left: 10px;
    }
  }

  .seach_hui {
    background: rgba(242, 246, 250, 1);

    .seach_bottom {
      height: 72px;
      border-radius: 2px;
      display: flex;
      align-items: center;
      padding-left: 27px;
    }
  }
}
</style>



